.summary-wrapper {
  padding: 1rem;
}

.summary-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.summary-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff4eb;
  border-radius: 20px;
  padding: 1rem;
  margin: 1rem 0;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  cursor: pointer;
}

.summary-left {
  flex: 1;
}

.summary-city {
  font-size: 1.3rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.summary-meta {
  font-size: 0.95rem;
  color: #444;
  line-height: 1.4;
}

.summary-cover {
  width: 100px;
  height: 70px;
  object-fit: cover;
  border-radius: 12px;
}
